import { mapActions, mapGetters } from 'vuex';
import Plus from 'vue-material-design-icons/Plus';
import TrashCan from 'vue-material-design-icons/TrashCan';
import PlusBoxMultiple from 'vue-material-design-icons/PlusBoxMultiple';
import FileUpload from 'vue-material-design-icons/FileUpload';
import Star from 'vue-material-design-icons/Star';
import DownloadOutline from 'vue-material-design-icons/DownloadOutline';
import { Modal, Actions, ActionButton, Button } from '@nextcloud/vue';
import moment from '@nextcloud/moment';
import logger from '../services/logger.js';
import { allMimes } from '../services/AllowedMimes.js';
import FetchFilesMixin from '../mixins/FetchFilesMixin.js';
import FilesByMonthMixin from '../mixins/FilesByMonthMixin.js';
import FilesSelectionMixin from '../mixins/FilesSelectionMixin.js';
import FilesListViewer from '../components/FilesListViewer.vue';
import EmptyContent from '../components/EmptyContent.vue';
import File from '../components/File.vue';
import Loader from '../components/Loader.vue';
import AlbumCreationForm from '../components/AlbumCreationForm.vue';
import AlbumPicker from '../components/AlbumPicker.vue';
export default {
  name: 'Timeline',
  components: {
    EmptyContent,
    AlbumCreationForm,
    AlbumPicker,
    FilesListViewer,
    Loader,
    File,
    Modal,
    Actions,
    ActionButton,
    Button,
    Plus,
    TrashCan,
    FileUpload,
    PlusBoxMultiple,
    Star,
    DownloadOutline
  },
  filters: {
    /**
     * @param {string} date - In the following format: YYYYMM
     */
    dateMonth(date) {
      return moment(date, 'YYYYMM').format('MMMM');
    },

    /**
     * @param {string} date - In the following format: YYYYMM
     */
    dateYear(date) {
      return moment(date, 'YYYYMM').format('YYYY');
    }

  },
  mixins: [FetchFilesMixin, FilesSelectionMixin, FilesByMonthMixin],

  beforeRouteLeave(to, from, next) {
    window.scrollTo(0, 0);
    next();
  },

  props: {
    onlyFavorites: {
      type: Boolean,
      default: false
    },
    mimesType: {
      type: Array,
      default: () => allMimes
    },
    onThisDay: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      loadingCount: 0,
      showAlbumCreationForm: false,
      showAlbumPicker: false
    };
  },

  computed: { ...mapGetters(['files']),

    /** @type {boolean} */
    shouldFavorite() {
      // Favorite all selection if at least one file is not on the favorites.
      return this.selectedFileIds.some(fileId => this.$store.state.files.files[fileId].favorite === 0);
    }

  },
  methods: { ...mapActions(['deleteFiles', 'toggleFavoriteForFiles', 'downloadFiles']),

    getContent() {
      this.fetchFiles('', {
        mimesType: this.mimesType,
        onThisDay: this.onThisDay,
        onlyFavorites: this.onlyFavorites
      });
    },

    openViewer(fileId) {
      const file = this.files[fileId];
      OCA.Viewer.open({
        path: file.filename,
        list: Object.values(this.fileIdsByMonth).flat().map(fileId => this.files[fileId]),
        loadMore: file.loadMore ? async () => await file.loadMore(true) : () => [],
        canLoop: file.canLoop
      });
    },

    openUploader() {// TODO: finish when implementing upload
    },

    async addSelectionToAlbum(albumId) {
      this.showAlbumPicker = false;

      try {
        this.loadingCount++; // TODO: finish add to album
        // await this.toggleFavoriteForFiles({ fileIds: this.selection, favoriteState: true })
      } catch (error) {
        logger.error(error);
      } finally {
        this.loadingCount--;
      }
    },

    async favoriteSelection() {
      try {
        this.loadingCount++;
        await this.toggleFavoriteForFiles({
          fileIds: this.selectedFileIds,
          favoriteState: true
        });
      } catch (error) {
        logger.error(error);
      } finally {
        this.loadingCount--;
      }
    },

    async unFavoriteSelection() {
      try {
        this.loadingCount++;
        await this.toggleFavoriteForFiles({
          fileIds: this.selectedFileIds,
          favoriteState: false
        });
      } catch (error) {
        logger.error(error);
      } finally {
        this.loadingCount--;
      }
    },

    async deleteSelection() {
      try {
        this.loadingCount++; // Need to store the file ids so it is not changed before the deleteFiles call.

        const files = this.selectedFileIds;
        this.$emit('uncheck-items', files);
        await this.deleteFiles(files);
      } catch (error) {
        logger.error(error);
      } finally {
        this.loadingCount--;
      }
    },

    async downloadSelection() {
      try {
        this.loadingCount++;
        await this.downloadFiles(this.selectedFileIds);
      } catch (error) {
        logger.error(error);
      } finally {
        this.loadingCount--;
      }
    }

  }
};